@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&family=Poppins:wght@400;500&family=Roboto:wght@400;500;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}:root{--violet: #0000ff21;--violet-light: rgba(0, 0, 255, 0.24);--color-primary: #19c37d;--color-secondary: #715fde;--color-label: #551A8B;--color-error: #ef4146;--text-primary: #202123;--text-default: #353740;--text-secondary: #6e6e80;--text-disabled: #acacbe;--primary-color: #333;--secondary-color: #999;--button-bg-color: #fff;--input-bg-color: #f5f5f5;--white: white;--bg-primary: rgba(247, 247, 248, 1);--bg-secondary: #f19307;--container-max-width: 1200px}h1,h2,h3,h4,p{font-family:'Montserrat', sans-serif}h1,.headline-1{font-size:27px;font-weight:700}h2,.headline-2{font-size:24px;font-weight:500}h3,.headline-3{font-size:24px;font-weight:700}h4,.headline-4{font-size:18px;font-weight:500}p,.body-text{margin:0;font-size:16px;font-weight:400}.btn-link{font-size:18px;font-weight:500}@media (min-width: 768px){h1,.headline-1{font-size:35px}h2,.headline-2{font-size:28px}h3,.headline-3{font-size:27px}h4,.headline-4{font-size:20px}p,.body-text{font-size:18px}}@media (min-width: 992px){h1,.headline-1{font-size:43px}h2,.headline-2{font-size:38px}h3,.headline-3{font-size:35px}h4,.headline-4{font-size:24px}}

.app{position:relative;width:100%;height:100vh;margin:0 auto;box-sizing:border-box;background-color:white}@media (max-width: 768px){.app{width:100%}}

.home__wrapper{display:flex;height:100vh;background-position:right 0 top;background-size:80% auto;background-repeat:no-repeat}.home__content{width:35%;color:#022a71;padding-left:20px}.home__content h1{font-size:48px}.home__content p{font-size:24px;max-width:90%}.home__content button{margin-top:40px;border-radius:30px;width:200px;height:50px;font-size:24px;outline:none;border:#a3c0f5;background-color:#a3c0f5;color:#022a71;cursor:pointer;transition:background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease}.home__content button:hover{color:var(--button-bg-color);border:1px solid var(--bg-secondary);background-color:var(--bg-secondary)}@media (max-width: 991.98px){.home__wrapper{background-size:80% auto}.home__content h1{margin:2rem auto;font-size:32px}.home__content button{width:150px;height:35px;font-size:18px}}@media (max-width: 767.98px){.home__wrapper{margin:0 40px;background-position:center bottom 45%}.home__content{width:100%;padding-left:0}.home__content h1{margin:2rem auto;font-size:24px;width:80%;text-align:center}.home__content p{margin:1rem auto;font-size:18px;width:80%;text-align:center}.home__content button{position:absolute;bottom:20px;left:50%;margin-right:-50%;transform:translate(-50%, -50%);width:150px;height:35px;font-size:18px}}@media (max-width: 575.98px){.home__wrapper{margin:0 20px;display:flex;height:100vh;background-size:90% auto;background-repeat:no-repeat;background-position:center bottom 40%}}

.logo{height:70px;width:auto}

.nav{display:flex;align-items:center;justify-content:space-around;margin-right:20px}@media (max-width: 768px){.nav{margin-right:5px}}.nav__home,.nav__practice,.nav__dictionary,.nav__my-dictionary{display:flex;justify-content:center;align-items:center;font-size:20px;margin:0 15px;font-weight:500;text-decoration:none;color:#032c76}.nav__home.active,.nav__practice.active,.nav__dictionary.active,.nav__my-dictionary.active{color:#f19307;border-radius:25px}

.header{display:flex;background-color:#ffffff;width:100%;height:70px;justify-content:space-between;border-bottom:1px solid #a3c0f5}.header__nav{display:flex;justify-content:center;align-items:center}@media (max-width: 768px){.header__nav{display:none}}.header__burger-menu{display:none;justify-content:center;align-items:center}@media (max-width: 768px){.header__burger-menu{display:flex;margin-right:30px}}

.menu{display:flex;flex-direction:row;list-style-type:none;margin:0;padding:0}.menu>li{margin:0 1rem;overflow:hidden}.menu-button-container{display:none;height:100%;width:30px;cursor:pointer;flex-direction:column;justify-content:center;align-items:center}#menu-toggle{display:none}.menu-button,.menu-button::before,.menu-button::after{display:block;background-color:#022a71;position:absolute;height:4px;width:30px;transition:transform 400ms cubic-bezier(0.23, 1, 0.32, 1);border-radius:2px}.menu-button::before{content:'';margin-top:-8px}.menu-button::after{content:'';margin-top:8px}#menu-toggle:checked+.menu-button-container .menu-button::before{margin-top:0px;transform:rotate(405deg)}#menu-toggle:checked+.menu-button-container .menu-button{background:rgba(255,255,255,0)}#menu-toggle:checked+.menu-button-container .menu-button::after{margin-top:0;transform:rotate(-405deg)}@media (max-width: 768px){.menu-button-container{display:flex}.menu{z-index:100;position:absolute;top:0;margin-top:70px;left:0;flex-direction:column;width:100%;justify-content:center;align-items:center}#menu-toggle ~ .menu li{height:0;margin:0;padding:0;border:0;transition:height 400ms cubic-bezier(0.23, 1, 0.32, 1)}#menu-toggle:checked ~ .menu li{height:2.5em;padding:10px;transition:height 400ms cubic-bezier(0.23, 1, 0.32, 1)}#menu-toggle:checked ~ .menu li:last-child{padding-bottom:20px;box-shadow:0 10px 10px -5px rgba(0,0,0,0.5)}.menu>li{display:flex;justify-content:center;margin:0;padding:0.5em 0;width:100%;color:white;background-color:white}}

.dictionary{width:90%;margin:0 auto}.dictionary__header{width:90%;margin:0 auto;height:70px;display:flex;justify-content:space-around;align-items:center}@media (max-width: 768px){.dictionary__header{flex-direction:column;padding:20px 0;height:auto;width:100%}}.dictionary__header button{padding:8px 15px;background-color:var(--button-bg-color);color:var(--primary-color);border:1px solid var(--primary-color);border-radius:4px;font-size:18px;text-decoration:none;cursor:pointer;white-space:nowrap;transition:background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease}@media (max-width: 575.98px){.dictionary__header button{width:100%;margin-top:20px}}.dictionary__header button:hover{color:var(--button-bg-color);border:1px solid var(--bg-secondary);background-color:var(--bg-secondary)}.dictionary__header p{font-size:24px}@media (max-width: 768px){.dictionary__header p{margin-bottom:20px}}.dictionary__actions{display:flex}@media (max-width: 575.98px){.dictionary__actions{flex-direction:column;width:100%}}.dictionary__main{width:60%;margin:0 auto}@media (max-width: 768px){.dictionary__main{width:100%}}.dictionary__modal_p{text-align:center;font-size:24px}

.modal{display:block;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.4)}.modal__content{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);z-index:10;background-color:#fefefe;padding:20px;border:1px solid #888;border-radius:20px;box-shadow:2px 2px 10px grey;width:30%}@media (max-width: 768px){.modal__content{width:80%}}.modal__content span{color:#aaa;float:right;font-size:28px;font-weight:bold}.modal__content span:hover,.modal__content span:focus{color:black;text-decoration:none;cursor:pointer}.modal__content .modal__content_title{margin-bottom:20px;font-size:24px;font-weight:300;display:flex;align-items:center;justify-content:center}.modal__control{display:flex;justify-content:center;align-items:center;margin-top:20px}.modal__control_btn{margin-bottom:20px;width:25%;outline:none;border:1px solid orange;color:white;background-color:orange;border-radius:20px;cursor:pointer;height:30px;transition:30ms;font-size:20px;font-weight:bold}.modal__control_btn:hover{transform:scale(1.1)}

.word__form{display:flex;justify-content:space-around;align-items:center;flex-direction:column}.word__form input{border-radius:5px;border:1px solid #d9dadb;outline:none;background-color:white;width:200px;height:40px;font-size:18px;padding-left:15px;box-shadow:0 0 5px #b1b2b3}.word__form input:last-child{margin-top:20px}

.word{display:flex;align-items:center;justify-content:space-between}.word__eng{font-size:18px;font-weight:bold}.word__rus{font-size:18px}.word__del{width:20px;height:20px;color:var(--primary-color);font-size:18px;cursor:pointer;text-align:center;margin-right:20px}.word__line{border-bottom:1px solid #b8b5b5;margin:5px 0}

.words__wrapper{width:100%;margin:0 auto}

.search{width:100%}.search__field{box-sizing:border-box;padding:8px 15px;background-color:var(--button-bg-color);color:var(--primary-color);border:1px solid var(--primary-color);border-radius:4px;font-size:18px;transition:border-color 0.3s ease;margin-right:10px}@media (max-width: 768px){.search__field{padding:8px 10px}}@media (max-width: 575.98px){.search__field{width:100%;margin-right:0}}.search__field:focus{outline:none;border-color:#2196f3;box-shadow:0 0 0 2px rgba(33,150,243,0.3)}

.notification_wrapper{display:flex;justify-content:center;align-items:center;text-align:center;padding:5px;width:200px;height:50px;color:#ffffff;border-radius:5px;border:1px solid #f39c1b;background-color:#f39c1b;position:absolute;bottom:40px;right:20px}

.pagination{display:flex;justify-content:center;align-items:center;padding:50px 0}@media (max-width: 768px){.pagination{display:flex;flex-direction:column;justify-content:center}}.pagination__button{margin:0 8px;padding:8px 16px;background-color:var(--button-bg-color);color:var(--primary-color);border:1px solid var(--primary-color);border-radius:4px;cursor:pointer;transition:background-color 0.3s ease-in-out}.pagination__button:disabled{opacity:0.5;cursor:not-allowed}.pagination__button:hover:not(:disabled){color:var(--button-bg-color);border:1px solid var(--bg-secondary);background-color:var(--bg-secondary)}.pagination__text{font-weight:bold;margin:0 8px;font-size:16px;color:var(--primary-color)}.pagination__form{display:flex;align-items:center;margin-left:20px}@media (max-width: 768px){.pagination__form{margin-top:20px}}.pagination__form input[type="number"]{width:60px;padding:6px 8px;border:1px solid var(--primary-color);border-radius:4px;font-size:16px;color:var(--primary-color)}.pagination__form input[type="number"]:focus{outline:none;border-color:#2196f3;box-shadow:0 0 0 2px rgba(33,150,243,0.3)}.pagination__form button[type="submit"]{margin-left:8px;padding:8px 16px;background-color:var(--button-bg-color);color:var(--primary-color);border:1px solid var(--primary-color);border-radius:4px;cursor:pointer;transition:background-color 0.3s ease-in-out}.pagination__form button[type="submit"]:hover{color:var(--button-bg-color);border:1px solid var(--bg-secondary);background-color:var(--bg-secondary)}

.message{text-align:center;margin-top:100px;color:orange;font-size:40px}

.input__wrapper{display:flex;justify-content:space-around;align-items:center;width:35%;height:100px;color:#032c76}.input__wrapper input{width:250px;height:50px;border:1px solid #dce5f6;border-radius:10px;background-color:#ebf0fa;color:#022a71;padding-left:15px;outline:none;font-size:30px}.input__wrapper input.mistake{border:1px solid #f11a1a;background-color:#f8eded}.btn__control{display:flex;justify-content:center;align-items:center;width:50px;height:50px;margin:0 5px;font-size:30px;font-weight:700;color:#6472a5;cursor:pointer}

.card{display:flex;justify-content:center;align-items:center;width:40%;height:270px;margin-top:25px;border:1px solid #f8f1e5;color:#032c76;box-shadow:inset 0 0 20px 10px #d7d6d4;border-radius:10px}@media (max-width: 768px){.card{width:80%}}.card p{font-size:45px}@media (max-width: 768px){.card p{font-size:35px}}

.practice{display:flex;flex-direction:column;justify-content:center;align-items:center}.practice__switcher{width:100%;height:70px;display:flex;justify-content:flex-start;align-items:center}@media (max-width: 768px){.practice__switcher{justify-content:center}}

.switcher{display:inline-block;height:30px;margin:3px 10px;padding:4px;background-color:#fff;width:200px;border-radius:30px;border:solid 1px #ddd;position:relative}.switcher__input{display:none}.switcher__label{float:left;width:50%;font-size:14px;color:#032c76;line-height:30px;text-align:center;cursor:pointer;position:inherit;z-index:10}.switcher__toggle{position:absolute;float:left;height:30px;width:50%;font-size:12px;line-height:30px;cursor:pointer;background:#bacef3;border-radius:30px;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);left:5px;top:4px;transition:left 0.25s ease-out;will-change:transform}.switcher__input_rus:checked ~ .switcher__toggle{left:100px}

.my-dictionary{width:90%;margin:0 auto;min-height:575px}@media (max-width: 768px){.my-dictionary{width:90%}}.my-dictionary__header{width:100%;height:70px;display:flex;justify-content:space-around;align-items:center}@media (max-width: 768px){.my-dictionary__header{flex-direction:column;padding:20px 0;height:auto}}.my-dictionary__header p{font-size:24px}@media (max-width: 768px){.my-dictionary__header p{margin-bottom:20px}}.my-dictionary__actions{display:flex}@media (max-width: 575.98px){.my-dictionary__actions{flex-direction:column;width:100%}}.my-dictionary__main{margin:0 auto;width:60%}@media (max-width: 768px){.my-dictionary__main{width:100%}}.my-dictionary__main b{font-size:18px}.my-dictionary__main_line{border-bottom:1px solid #b8b5b5;margin:5px 0}

.word-filter{display:flex;align-items:center}.word-filter select{position:relative;padding:8px;font-size:17px;background-color:var(--button-bg-color);color:var(--primary-color);border:1px solid var(--primary-color);border-radius:4px;cursor:pointer;height:39px;transition:border-color 0.3s ease-in-out}@media (max-width: 575.98px){.word-filter select{margin-top:20px;width:100%}}.word-filter select:focus{outline:none;border-color:#2196f3;box-shadow:0 0 0 2px rgba(33,150,243,0.3)}

